/* Play button, toggle between play & pause */
const Play = ({ play, onClick}) => {
  return(
    <div>
    <audio id="audioFailsafe" preload="none">
      <source src={failsafeSFX} type="audio/ogg" />
      </audio>
      <FontAwesomeIcon icon={play ? faPause : faPlay} fixedWidth className="fontIcon play" onClick={onClick}/>
    </div>
    
  )
}

/* Play button, toggle between play & pause, Play button for Wish 8 */
const PlayHope = ({ play, onClick}) => {
  return(
    <div>
    <audio id="audioHope" preload="none">
      <source src={hopeSFX} type="audio/ogg" />
      </audio>
      <FontAwesomeIcon icon={play ? faPause : faPlay} fixedWidth className="playHope" onClick={onClick}/>
      <img src={travelerIMG} alt="traveler" className="traveler"/>
    </div>
  )
}

/* Play button, toggle between play & pause, Play button for Wish 8 */
const PlayDrifter = ({ play, onClick}) => {
  return(
    <div>
    <audio id="audioDrifter" preload="none">
      <source src={drifterSFX} type="audio/ogg" />
      </audio>
      <FontAwesomeIcon icon={play ? faPause : faPlay} fixedWidth className="fontIcon playDrifter" onClick={onClick}/>
    </div>
  )
}



