function Wish1Effect() {
    /* display modal */
    const modal = document.getElementById("wish1-modal");
    
    modal.style.display= "block";
    const itemSFX = document.querySelector(".itemSfx");
    itemSFX.play();

    /* hide animation after play */
    const item = document.querySelector(".item");
   
       /* get loot drop */
       const loot = document.getElementById("drop");
       /* loot.id ="drop"; */
       loot.id = "afterDrop";

       loot.addEventListener("animationend", ()=>{
        /* loot.id = "afterDrop"; */
        loot.id ="drop";
       });


    /* close modal & pause audio */
    const close1 = document.querySelector(".close1");
    close1.addEventListener("click", function(e) {
        e.preventDefault();
        modal.style.display = "none";
        item.style.display = "block";
    });
}

