function Wish2Effect() {
    /* display modal */
    const modal = document.getElementById("wish2-modal");
    
    modal.style.display= "block";

     /* open chest on click, play sfx, get ship drop  */
    let chest = document.querySelector(".chest"); 

     chest.addEventListener("click", ()=>{

        chest.classList.add("chestOpen");
        const chestSfx = document.querySelector(".chestSfx");
        chestSfx.play();
       
        /* delay ship drop, get loot drop && play sfx */            
            setTimeout(function(){
            const ship = document.querySelector(".shipWrapper");
            
            const sfx = document.querySelector(".shipSfx");
            
            ship.classList.remove("dropShip");
            ship.classList.add("getShip");
            sfx.play();

            ship.addEventListener("animationend", () =>{
            ship.classList.remove("getShip");
            ship.classList.add("dropShip");
            });
               
            },1500);
   
        });
     
     

    /* close modal & pause audio */
    const close2 = document.querySelector(".close2");
    close2.addEventListener("click", function(e) {
        e.preventDefault();
        chest.classList.remove("chestOpen");
        modal.style.display= "none";
    });
}

