 /*///////////////////////////////////////////////////////////////////////
                                        Wish 14 - Ahamkara Eggs
///////////////////////////////////////////////////////////////////////*/


function Wish14Effect() {
    /* display modal */
    const modal = document.getElementById("wish14-modal");
    
    modal.style.display = "block";

    /* randomize images (eggs) */
    let eggs = document.querySelectorAll(".egg");

    //get window width & height
    const winWidth = window.innerWidth;
    const winHeight = window.innerHeight;

    for (let i = 0; i< eggs.length; i++) {
      //shortcut the current div in the list
      let thisEgg = eggs[i];

        //returns a random number between a min & max
        
        //get random numbers for each element
        let randomTop = getRandomNumber(0, winHeight);
        let randomLeft = getRandomNumber(0, winWidth);
        
        //update top & left position
        thisEgg.style.top = randomTop + "px";
        thisEgg.style.left = randomLeft + "px";

         /* egg onclick Event, let eggs vanish when clicked */
        eggs[i].addEventListener("click", ()=>{

            eggs[i].style.display = "none";

            /* // give loot, display random image from loot table // */

            /* get loot drop && play sfx */
            const itemSfx14 = document.querySelector(".itemSfx14");
            const eggLoot = document.getElementById("eggLoot"); //drop = display none

                document.getElementById('eggLoot').id='eggLootDrop';
                itemSfx14.play();
           

            eggLoot.addEventListener("animationend", ()=>{
                eggLoot.id='eggLoot';
            });
            
            

            /* randomize loot drops */
            const lootDrop = document.querySelector(".loot");
            const lootTable = [auto,handcannon,rocket,scout,shotgun,sniper,sword,
                                hunterHelmet,hunterArms,hunterChest,hunterBoots,hunterClass,
                                titanHelmet,titanArms,titanChest,titanBoots,titanClass,
                                WarlockHelmet,WarlockArms,WarlockChest,WarlockBoots,WarlockClass];
            
            let num = Math.floor(Math.random() * lootTable.length);
            lootDrop.src = lootTable[num];

            
            
        },false);
 
    };
    
    function getRandomNumber(min,max) {
        return Math.random() * (max - min) + min;
    };


    /* close modal & pause audio */
    const close14 = document.querySelector(".close14");
    close14.addEventListener("click", function(e) {
        e.preventDefault();
        modal.style.display= "none";
    });
}

 /*///////////////////////////////////////////////////////////////////////
                                 Wish 15 - The Helmet stayed on (   )
///////////////////////////////////////////////////////////////////////*/
function Wish15Effect() {
    /* display modal */
    const modal = document.getElementById("wish15-modal");
    
    modal.style.display= "block";

    /* text to speech */
    let txt = `I wish to be feared.
    Mara stared at the Wall of Wishes. She had no more bargains to make. Her plans were in motion.    
    There was nothing left but the wait.    
    She pulled a sidearm from a hidden holster and cracked a dozen rounds into the Wall.    
    The cosmic balance shifted. Somewhere in the Dreaming City, Riven heard the Queen's wish and a thousand shrieking tears in reality cut through the space in front of her.
    Lord Shaxx suddenly blocked Mara's view of the Wall.    
    "YES, AND WE'D ALL BE DEAD!" He was screaming at no one, with a mug of caffeine in his fist.    
    He started, almost spilling his coffee. "Where are we?"    
    Mara slapped the mug out of his hand. It shattered on the floor. She shoved a weathered book in his face. "I told you there would come a time when I collect for the Reef Wars. Read this. Out loud."
    "No one tells me what to do," he said, grabbing the book and incinerating it in a bolt of Striker lightning. "I can recite The Tempest by heart." And he did. Mara sat and listened. They stayed for a long time.
    The helmet stayed on.`;

    let speak = new SpeechSynthesisUtterance(txt);
    speak.lang = 'en-UK';

    speechSynthesis.speak(speak);

    /* close modal & pause audio */
    const close15 = document.querySelector(".close15");
    close15.addEventListener("click", function(e) {
        e.preventDefault();
        window.speechSynthesis.pause();
        window.speechSynthesis.cancel();
        modal.style.display= "none";
    });
}




